@font-face {
  font-family: "Corbel";
  src: local("Corbel"), url(./fonts/corbel.ttf) format("truetype");
}

* {
  box-sizing: border-box;
}

html,
body {
  max-width: 100% !important;
  overflow-x: hidden !important;
}

body {
  margin: 0;
  color: #555555;
}

body,
input,
button,
select {
  font-family: Corbel;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 18px;
}

@media screen and (max-width: 480px) {
  body,
  input,
  button,
  select {
    font-size: 14px;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

h1 {
  border-bottom: 1px solid gainsboro;
}

table {
  width: 100%;
  border-collapse: collapse;
}

table thead tr th,
table tbody tr td {
  border: 1px solid gainsboro;
  padding: 0 3px 2px 3px;
}

table thead tr th {
  font-weight: bold;
}

button {
  box-shadow: 3px 3px 4px rgba(0, 0, 0, 0.3);
  background-color: gainsboro;
  border: 1px solid gainsboro;
  border-radius: 5px;
  padding: 3px 7px;
  cursor: pointer;
}

button:hover {
  background-color: #bbbbbb;
  border: 1px solid #bbbbbb;
}

button.inactive, button:hover.inactive {
  cursor: default;
  background-color: #666666;
  border: 1px solid #666666;
  color: white;
}

input {
  border: 1px solid gainsboro;
  padding: 3px 5px;
}

select {
  border: 1px solid gainsboro;
  width: 100%;
  color: #666666;
  min-width: 70px;
}

/* CLASSES: */

.flexRow,
.flexRowStretch,
.flexRowRight {
  display: flex;
  /* align-items: center; */
}

.flexRowStretch {
  justify-content: space-between;
}

.flexRowRight {
  justify-content: flex-end;
}

.flexRow > * {
  margin-right: 20px;
}

.label {
  font-weight: bold;
}
